.tui-pagination * {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
  }
  .container-pagination {
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-bottom: 40px;
      max-width: 320px;
    }
    @media screen and (min-width: 768px) {
      margin-bottom: 60px;
      max-width: 658px;
    }
  }
  
  .tui-ico-ellipsis,
  .tui-ico-first,
  .tui-ico-last,
  .tui-ico-next,
  .tui-ico-prev {
    overflow: hidden;
  }
  
  .tui-pagination {
    height: 55px;
    line-height: normal;
    text-align: center;
    font-size: 0;
    margin: 0;
  }
  
  .tui-pagination > .tui-page-btn {
    display: inline-block;
    position: relative;
    width: 40px;
    padding: 13px 0 11px;
    margin-left: 0;
    color: var(--text-color);
    font-size: 12px;
    font-weight: normal;
    line-height: 1.34;
    text-decoration: none;
    vertical-align: middle;
    border: 0px;
  }
  
  .tui-pagination .tui-page-btn:hover {
    background-color: var(--color-back-pagination);
  }
  
  .tui-pagination .tui-is-selected,
  .tui-pagination strong {
    color: #fff;
    background: var(--color-accent);
    border-color: var(--color-accent);
    border-radius: 5px;
    cursor: default;
  }
  
  .tui-pagination .tui-is-selected:hover {
    background-color: var(--color-accent-link);
    border-color: var(--color-accent-link);
    border-radius: 5px;
  }
  
  .tui-pagination .tui-first-child.tui-is-selected,
  .tui-pagination strong.tui-first-child {
    margin-left: 0;
  }
  
  .tui-pagination .tui-first-child.tui-is-selected {
    border-left: 1px solid #ff6b08;
  }
  
  .tui-pagination .tui-last-child.tui-is-selected {
    border-right: 1px solid #ff6b08;
  }
  .tui-pagination .tui-first,
  .tui-pagination .tui-last {
    font-size: 0;
    border: none;
    display: none;
  }
  // .tui-pagination .tui-first,
  .tui-pagination .tui-prev,
  .tui-pagination .tui-next {
    overflow: hidden;
    height: 40px;
    padding: 0;
    font-size: 0;
    line-height: 36px;
    white-space: nowrap;
    font-weight: normal;
    border: 1px solid var(--color-back-pagination);
    background: var(--color-back-pagination);
    border-radius: 5px;
  }
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    height: 40px;
    padding: 0;
    font-size: 0;
    line-height: 36px;
    white-space: nowrap;
    font-weight: normal;
  }
  
  .tui-pagination .tui-prev-is-ellip {
    border: 0;
  }
  
  .tui-pagination .tui-next-is-ellip {
    border: 0;
  
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  
  .tui-pagination .tui-first + .tui-prev,
  .tui-pagination .tui-next + .tui-last {
    border-left: 0;
  }
  
  .tui-pagination .tui-ico-prev {
    background-image: url('.././images/pagination/arrow-left.png');
    display: inline-block;
    vertical-align: middle;
    background-position: center;
    background-repeat: repeat;
    background-size: cover;
    font-size: 0;
  }
  
  .tui-pagination .tui-ico-next {
    background-image: url('.././images/pagination/arrow-right.png');
    display: inline-block;
    vertical-align: middle;
    background-position: center;
    background-repeat: repeat;
    background-size: cover;
    font-size: 0;
  }
  .tui-pagination .tui-ico-ellip {
    background: url(data:images/png;base64,)
      no-repeat 0 0;
    display: inline-block;
    vertical-align: middle;
    font-size: 0px;
  }
  
  .tui-pagination .tui-prev-is-ellip,
  .tui-pagination .tui-next-is-ellip {
    padding: 0;
    line-height: 1;
  }
  
  .tui-pagination .tui-prev {
    margin-right: 10px;
  }
  
  .tui-pagination .tui-next {
    margin-left: 10px;
  }
  
  .tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px;
  }
  
  .tui-pagination .tui-ico-prev {
    width: 6px;
    height: 10px;
  }
  
  .tui-pagination .tui-ico-next {
    width: 6px;
    height: 10px;
  }
  
  .tui-pagination .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px;
  }
  
  .tui-pagination .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 20px;
    vertical-align: top;
    background-position: 0px -24px;
  }
  
  .tui-pagination .tui-is-disabled:hover {
    background: #fff;
    cursor: default;
  }
  
  .tui-pagination .tui-is-disabled .tui-ico-prev {
    background-position: 0px;
  }
  
  .tui-pagination .tui-is-disabled .tui-ico-first {
    background-position: 0px;
  }
  
  .tui-pagination .tui-is-disabled .tui-ico-next {
    background-position: 0px;
  }
  
  .tui-pagination .tui-is-disabled .tui-ico-last {
    background-position: 0px;
  }
  
  .tui-page-inputs {
    display: inline-block;
    vertical-align: top;
  }
  
  .tui-page-inputs .tui-page-input {
    display: inline-block;
    width: 10px;
    height: 12px;
    padding: 8px 0 6px;
    font-size: 12px;
    text-align: center;
    color: #333;
    border: 0;
    outline: none;
  }
  
  .tui-page-inputs .tui-page-current {
    color: #ff6b08;
  }
  
  .tui-page-separator {
    display: inline-block;
    font-size: 12px;
    height: 12px;
    padding: 8px 0 6px;
    vertical-align: top;
  }
  